.ui-issue {
  display: flex;
}
.ui-issue .check-box {
  width: 20px;
  height: 20px;
  margin-right: 10px;
  border-radius: 50%;
  border: 1px solid #cacaca;
  margin-top: 10px;
}
.ui-issue .main {
  flex: 1 1;
  border-bottom: 1px solid #e9e9e9;
  line-height: 20px;
  padding: 10px 0;
  display: flex;
}
.ui-issue .main .title {
  flex: 1 1;
  margin-right: 5px;
  cursor: default;
}
.ui-issue .main .flag {
  width: 20px;
  color: #cacaca;
}
